$purple-primary: #6C37A6;
$purple-secondary: #A45CF2;
$warning: #ffc107;

.side-bar {
    background-color: #54A62E;
    border-radius: 5px;
    padding: 1em;
    color:#fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 600px;
    box-shadow:
        0 1.2px 2.2px rgba(0, 0, 0, 0.02),
        0 2.8px 5.3px rgba(0, 0, 0, 0.028),
        0 5.3px 10px rgba(0, 0, 0, 0.035),
        0 9.4px 17.9px rgba(0, 0, 0, 0.042),
        0 17.5px 33.4px rgba(0, 0, 0, 0.05),
        0 42px 80px rgba(0, 0, 0, 0.07)
        ;

    .box {
        display: flex;
        justify-content: start;
        background-color: #fff;
        color: #343a40;
        padding: 8px 15px;
        border: 1px solid transparent;
        border-radius: 5px;
        transition: all .1s ease-in-out;
        box-shadow:
            0 2.8px 2.2px rgba(0, 0, 0, 0.02),
            0 6.7px 5.3px rgba(0, 0, 0, 0.028),
            0 12.5px 10px rgba(0, 0, 0, 0.035),
            0 22.3px 17.9px rgba(0, 0, 0, 0.042),
            0 41.8px 33.4px rgba(0, 0, 0, 0.05),
            0 100px 80px rgba(0, 0, 0, 0.07)
            ;
        
        &.inverted{
            background-color: #343a40;
            color: #fff;

            &:hover{
                border: 1px solid #343a40;
                box-shadow: none;
            }
        }
        
        &.clickable{
            cursor: pointer;
            margin: 18px 0;
            font-weight: 300;
        }

        &.hover {
            &:hover{
                border: 1px solid #343a40;
                box-shadow: none;
            }
        }
    }
    
    .store-logo {
        display: flex;
        align-items: center;
    }

    h4 {
        padding: 0 8px;
        margin: 0;
    }
}


.pressDownButton {
    background-color: hsl(222, 100%, 95%);
    padding: 12px 24px;
    color: hsl(243, 80%, 62%);
    border-radius: 6px;
    border-bottom: 4px solid hsl(221, 89%, 85%);
    transition: all 0.1s ease-in-out;
}

.pressDownButton:hover {
    border-bottom-width: 0;
    margin-top: 3px;
}
